.menu {
    height: 100vh;
    background-color: #124E66;
    color: #ffffff;
}

.container {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 1fr 9fr 1fr;
}

.logo {
    font-size: 30px;
}

.logo:hover {
    color: #ffffff;
}

.link_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
}

.link {
    text-decoration: none;
    color: #ffffff;
}

.link_container:hover {
    background-color: #124e51;
}

.activeLink {
    color: darkorange;
}